.about_short {
  min-height: 543px;
  margin-top: 19px;
  background-color: #f2f2f2;
  .container {
    .row {
      .about {
        .intro {
          margin-top: 130px;
          @include intro;
        }
        h2 {
          @include smallheader;
        }
        .lines {
          @include lines;
          margin-top: 27px;
        }
        .main_text {
          @include featuretext;
          max-width: 485px;
          margin-top: 23px;
          span {
            letter-spacing: 4px;
          }
        }
      }
      .logo {
        img {
          width: 107.2%;
          height: auto;
          margin-top: 33px;
          margin-left: -29px;
        }
      }
    }
  }
}


