* {
  box-sizing: border-box;
}

body {
  font-family: "SourceSansPro";
}
header {
  min-height: 640px;
  background-color: $backblack;
  background-image: url("../img/photo.png");
  background-size: cover;
  .container {
    .navbar {
      color: $mainwhite;
      padding: 35px 0 0;
      #nav-icon3 {
        display: none;
        z-index: 3;
      }
      .logo {
        display: flex;
        justify-content: left;
        padding: 6px 0px 4px 2px;
        .lucid {
          width: 69px;
          height: 18px;
        }
        .onepage {
          width: 44px;
          height: 16px;
          display: block;
          margin: 2px 0 0 7px;
        }
      }
      nav {
        ul {
          display: flex;
          height: 16px;
          color: #fffefe;
          font-family: "SourceSansPro";
          font-size: 16px;
          font-weight: 600;
          margin-top: -2px;
          li {
            margin-left: 23px;
            a {
              text-decoration: none;
              color: $mainwhite;
              position: relative;
              &:after {
                background: none repeat scroll 0 0 transparent;
                bottom: -10px;
                content: "";
                display: block;
                height: 2px;
                left: 48%;
                position: absolute;
                background: $blue;
                transition: width 0.3s ease 0s, left 0.3s ease 0s;
                width: 0;
                transition: 0.3s;
                opacity: 0;
              }
              &:hover:after {
                opacity: 1;
                width: 48%;
                left: 0;
                transition: 0.3s;
              }
              &:before {
                background: none repeat scroll 0 0 transparent;
                bottom: -10px;
                content: "";
                display: block;
                height: 2px;
                left: 50%;
                position: absolute;
                background: $mainblue;
                transition: width 0.3s ease 0s, right 0.3s ease 0s;
                width: 0;
                transition: 0.3s;
                opacity: 0;
              }
              &:hover:before {
                width: 48%;
                left: 52%;
                opacity: 1;
                transition: 0.3s;
              }
            }
          }
        }
      }
    }
  }
  .introduction {
    .container {
      .row {
        .introduction_block {
          margin-top: 97px;
          .intro {
            color: $mainblue;
            font-size: 14px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 1.4px;
            font-family: "SourceSansPro";
          }
          h2 {
            margin-top: 22px;
            color: $mainwhite;
            font-size: 48px;
            font-weight: 600;
            line-height: 48px;
            letter-spacing: 0.05px;
            span:nth-child(1) {
              letter-spacing: -2.5px;
            }
            span:nth-child(2) {
              letter-spacing: 1.5px;
            }
          }
          .main_text {
            font-weight: 400;
            line-height: 26px;
            color: $softgray;
            max-width: 490px;
            font-size: 18px;
            margin-top: 24px;
            letter-spacing: 0.05px;
          }
          .mybutton {
            @include mybutton;
          }
          .viewbtn {
            @include viewbtn;
          }
        }
        .img_block {
          background: url("../img/devices_kopia.png") no-repeat bottom;
          background-size: 101%;
          height: 519px;
          margin-top: 58px;
          margin-left: 8%;
        }
      }
    }
  }
}

.shown {
  height: 340px !important;
}
.black {
  background-color: black;
}

#nav-icon3 span:nth-child(1) {
  top: 0px;
}

#nav-icon3 span:nth-child(2),
#nav-icon3 span:nth-child(3) {
  top: 14px;
}

#nav-icon3 span:nth-child(4) {
  top: 28px;
}

#nav-icon3.open span:nth-child(1) {
  top: 14px;
  width: 0%;
  left: 50%;
}

#nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon3.open span:nth-child(4) {
  top: 14px;
  width: 0%;
  left: 50%;
}

#nav-icon1,
#nav-icon2,
#nav-icon3,
#nav-icon4 {
  width: 45px;
  height: 36px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span,
#nav-icon3 span,
#nav-icon4 span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: $mainblue;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}


